






















	import {
		mapState,
		mapMutations,
		mapGetters
	} from 'vuex';
	export default {
		name:"password-authentication",
		data() {
			return {
				inputValue:'',
				bannerShowSync:this.bannerShow,
			};
		},
		computed:{
				...mapGetters([
					'getUserInfo'
					// ...
				]),
				isNotNeedPass(){
					return this.getUserInfo.nopaypwd==0?false:true;
				}
		},
		props:{
			bannerShow:{
				type:Boolean,
				default:false
			},
			amount:{
				type:String,
				default:'0.0'
			}
			
		},
			
		methods:{
			changep:function(){
				this.$emit('toNoPayPassSet');
			},
			closeBanner:function()
			{
				var that = this;
				that.inputValue = '';
				this.$emit('send-cancel',{
					show: false
				});
				
			},
			confirm:function(){
				var that = this;
// 				if (that.inputValue.trim() == '') {
// 				
// 					that.inputValue = '';
// 					
// 				} else {
				   
					//点击发送按钮时，通知父组件用户输入的内容
					this.$emit('send-pass', {
						content: that.inputValue.trim(),
						show:false,
						isNotNeedPass:that.isNotNeedPass
						
					});
					that.inputValue = '';
				// }
			},
			cancel:function(){
				var that = this;
				// that.bannerShow = false;
				that.inputValue = '';
				this.$emit('send-cancel',{
					show: false
				})
			}
		}
	}
